// Import
@import 'component/menu-fade'; //menu-fade, menu-fade_open
@import 'component/menu-bg';
// End of Import
@include menu-fade;
.menu-open {
    @include menu-fade_open;
    .c- {
        &nav {
            pointer-events: all !important;
            &_bg {
                // 框架內會發生的事 ↓
                @include menu-bg-curtain_open;
                @include menu-bg-slide_open;
            }
        }
    }
    .c-header_button_icon.-buger {
        height: 20px !important;
        // width: 0;
        span {
            width: 0;
        }
        &::before {
            transform-origin: right top;
            transform: rotate(-45deg);
            margin-top: -10%;
            margin-left: -20%;
            // top: -10%;
        }
        &::after {
            transform-origin: right bottom;
            transform: rotate(45deg);
            margin-left: -20%;
            // bottom: 0;
        }
    }
}

.c-nav {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    pointer-events: none;
    &_bg {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        // 框架內會發生的事 ↓
        @include menu-bg-curtain;
        @include menu-bg-slide;
    }
    &_content {
        .-dark {
            background-color: transparent !important;
        }
        width: 100%;
        height: 100%;
        position: relative;
        top: 0;
        left: 0;
        z-index: 200;
    }
}

.c-header {
    min-height: 100px;
    width: 100%;
    position: fixed;
    padding-top: 10px;
    z-index: 200;
    mix-blend-mode: difference;
    pointer-events: all;
    color: var(--primary);
    &_logo {
        height: 100%;
        position: absolute;
        display: inline-flex;
        align-items: center;
        width: 25%;
        left: 3vw;
        a {
            text-decoration: none;
        }
    }
    &_button {
        position: absolute;
        right: 3vw;
        height: 100%;
        width: 10vmin;
        display: inline-flex;
        align-items: center;
        justify-content: right;
        &:hover .-buger {
            height: 30px;
        }
        button {
            width: 100%;
            height: 100%;
            position: absolute;
            opacity: 0;
        }
        &_icon {
            &.-buger {
                margin-left: auto;
                transition: height .3s;
                // font-size: 0;
                pointer-events: none;
                position: relative;
                width: 30px;
                height: 20px;
                span {
                    transition: 1.5s cubic-bezier(.83, 0, 0, .99);
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    right: 0;
                    margin: auto;
                    width: 100%;
                    height: 2px;
                    background-color: var(--secondary);
                }
                &::before,
                &::after {
                    transition: 1s cubic-bezier(.83, 0, 0, .99);
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 2px;
                    background-color: var(--secondary);
                }
                &::before {
                    top: 0px;
                }
                &::after {
                    bottom: 0px;
                }
            }
        }
    }
}

