@mixin menu-fade_open {
    .-left-in,
    .-top-in,
    .-right-in,
    .-bottom-in {
        transition: 1s cubic-bezier(.61, 0, 0, .99);
        transition-delay: .2s;
        transform: translateX(0) translateY(0);
        opacity: 1;
    }
}

@mixin menu-fade {
    .-left-in,
    .-top-in,
    .-right-in,
    .-bottom-in {
        transition: 1s cubic-bezier(.61, 0, 0, .99), opacity .4s;
        opacity: 0;
    }
    .-left-in {
        transform: translateX(-200px);
    }
    .-right-in {
        transform: translateX(200px);
    }
    .-top-in {
        transform: translateY(-200px);
    }
    .-bottom-in {
        transform: translateY(200px);
    }
}