// 換行
br[-none] {
    display: none;
}

br[-none-m],
.-none-m {
    @media screen and (max-width: 1000px) {
        display: none;
    }
}

br[-none-d] {
    @media screen and (min-width: 1000px) {
        display: none;
    }
}

.-inline {
    margin: var(--typography-margin);
}

.-aa {
    text-transform: uppercase;
}

span {
    display: inline-block;
}

// 文字
h1 {
    margin: var(--typography-margin);
    font-weight: 300;
    font-size: calc(100px + 5vw);
    line-height: calc(100px + 4.5vw);
    letter-spacing: -2px;
    @include span;
}

h2 {
    margin: var(--typography-margin);
    font-weight: 300;
    font-size: calc(50px + 5vw);
    line-height: calc(50px + 4.5vw);
    letter-spacing: -1px;
    @include span;
}

h3 {
    margin: var(--typography-margin);
    font-weight: 300;
    font-size: calc(20px + 5vw);
    line-height: calc(20px + 4.5vw);
    @include span;
}

h4 {
    margin: var(--typography-margin);
    font-weight: 300;
    font-size: calc(20px + 3vw);
    line-height: calc(20px + 2.5vw);
    @include span;
}

h5,
h6 {
    margin: var(--typography-margin);
    font-weight: 700;
    font-size: calc(10px + 1vw);
    line-height: calc(10px + 0.5vw);
    @include span;
}

p,
a {
    margin: var(--typography-margin);
    font-size: calc(10px + .5vw);
    ;
    // max-width: 1200px;
}

.-left {
    text-align: left;
}

.-right {
    text-align: right;
}