// curtain
@mixin menu-bg-curtain {
    &.-curtain {
        display: flex;
        justify-content: center;
        .c-nav_bg_col {
            min-width: 300px;
            width: 25%;
            height: 0%;
            background-color: var(--secondary);
            @for $i from 1 to 5 {
                &:nth-child(#{$i}) {
                    transition: cubic-bezier(.81, .01, .15, .99) $i*0.1+.6s;
                }
            }
        }
    }
}

@mixin menu-bg-curtain_open {
    &.-curtain {
        .c-nav_bg_col {
            height: 100%;
        }
    }
}

// slide
@mixin menu-bg-slide {
    &.-slide {
        display: flex;
        .c-nav_bg_col {
            width: 100%;
            height: 0%;
            background-color: var(--secondary);
            transition: 1s cubic-bezier(.81, .01, .15, .99);
        }
    }
}

@mixin menu-bg-slide_open {
    &.-slide {
        .c-nav_bg_col {
            height: 100%;
        }
    }
}