.c-typogrid-title {
    overflow-x: hidden !important;
    @keyframes typogrid-waveing {
        0% {
            transform: translateX(calc(var(--typogrid-waveing)*-1));
        }
        50% {
            transform: translateX(var(--typogrid-waveing));
        }
        100% {
            transform: translateX(calc(var(--typogrid-waveing)*-1));
        }
    }
    &-images {
        // --typogrid-waveing: 200px;
        // animation: typogrid-waveing 60s infinite ease-in-out;
        transition: 3s cubic-bezier(.07, .22, .22, .97);
        position: absolute;
        margin-left: -100px;
        top: 4vh;
        z-index: 1;
        width: 23vw;
        min-width: 200px;
        height: 25vw;
        min-height: 200px;
    }
    &-english {
        // mix-blend-mode: difference;
        // width: auto;
        height: calc(20px + 9vw);
        pointer-events: none;
        &-1,
        &-2,
        &-3-2,
        {
            z-index: 3 !important;
        }
        position: relative;
        // z-index: -3;
        // margin: 0 20px;
        &-frame {
            width: 100%;
            overflow-x: hidden !important;
            min-width: 500px;
            margin: 5vw 0;
            --typogrid-waveing: 2.5vw;
            display: flex;
        }
        @for $i from 1 to 6 {
            &-#{$i} {
                animation: typogrid-waveing 10s infinite cubic-bezier(.84, .01, .23, 1);
                // margin: 0 10px;
                // animation-duration: 2s;
                animation-delay: #{-.18 * $i}s;
            }
        }
    }
    &-chinese {
        pointer-events: none;
        position: relative;
        // margin: 0 20px;
        height: calc(20px + 12vw);
        &-frame {
            width: 100vw;
            overflow-x: hidden !important;
            margin: 5vw 0;
            margin-left: 40vw;
            --typogrid-waveing: 4vw;
            display: flex;
        }
        @for $i from 1 to 6 {
            &-#{$i} {
                animation: typogrid-waveing 12s infinite cubic-bezier(.84, .01, .23, 1);
                // margin: 0 10px;
                // animation-duration: 2s;
                animation-delay: #{-.15 * $i - 2.3}s;
            }
        }
        // &-1,
        // &-2,
        &-3-1,
        &-4,
        &-5 {
            z-index: 3 !important;
        }
    }
}

.c-hero {
    &-subfirst {
        padding-top: 15vh;
    }
    &-info {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        max-width: 500px;
        margin-top: -5vw;
        margin-bottom: 10vh;
        &-en {
            justify-self: flex-start;
        }
        &-zh {
            margin-left: auto;
        }
    }
    &-smalltext {
        margin-bottom: 15vh;
    }
}